    box-sizing: border-box;
}

*, *:before, *:after {
    box-sizing: inherit;
}

body {
    margin: 0;
}

#content {
    min-height: 100vh;
    position: relative;
}

.header-section {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-position: 0% 35%;
    background-repeat: no-repeat;
    height: 400px;
    display: grid;
    place-items: center;
}

.header-text-container {
    border: 6px solid white;
    width: fit-content;
    padding: 20px;
    color: white;
}

.header-title,
.header-subtext {
    text-align: center;
}

.header-title {
    font-size: 6rem;
}

.header-subtext {
    font-size: 2rem;
}

.tab-section {
    display: flex;
    justify-content: center;
}

.tab-container {
    display: grid;
    grid-template: 25px / repeat(3, min-content);
    justify-content: center;
    font-weight: bold;
    align-content: center;
    column-gap: 5rem;
}

.tab {
    font-size: 1.2rem;
    width: 100px;
    text-align: center;
}

.tab-slider {
    width: 100px;
    height: 3px;
    background-color: rgb(67, 87, 61);
    margin-left: 0;
    transition: margin-left 1s ease-in-out;
}

.tab-slider-home {
    margin-left: 0;
}

.tab-slider-menu {
    margin-left: 11.3rem;
}

.tab-slider-contact {
    margin-left: 22.5rem;
}

.tabs-and-slider-container {
    width: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 50px;
}

.tab:hover {
    cursor: pointer;
}

.tab:hover:not(.current-tab) {
    font-size: 1.3rem;
}

.header-text-container,
.tab-section,
.main-section {
    font-family: "Times New Roman", sans-serif, monospace;
}

.main-section {
    display: flex;
    justify-content: center;
    height: 100%;
}

footer {
    background-color: black;
    display: flex;
    justify-content: center;
    color: white;
    position: absolute;
    width: 100%;
    bottom: 0;
}

.footer-text-container {
    padding: 10px;
}

@media screen and (max-width: 800px) {
    .header-section {
        height: 250px;
    }

    .header-title {
        font-size: 3rem;
    }

    .header-subtext {
        font-size: 1rem;
    }

    .tab-container {
        column-gap: 2rem;
    }

    .tab-slider-home {
        margin-left: 0;
    }
    
    .tab-slider-menu {
        margin-left: 8.3rem;
    }
    
    .tab-slider-contact {
        margin-left: 16.5rem;
    }
}

@media screen and (max-width: 400px) {
    .tab-container {
        column-gap: 1rem;
    }

    .tab {
        font-size: 1rem;
        width: 80px;
    }

    .tab-slider {
        width: 80px;
    }

    .tab-slider-home {
        margin-left: 0;
    }
    
    .tab-slider-menu {
        margin-left: 6rem;
    }
    
    .tab-slider-contact {
        margin-left: 12rem;
    }
}`, "",{"version":3,"sources":["webpack://./src/style-sheets/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;AAC1B;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,4HAAwI;IACxI,sBAAsB;IACtB,2BAA2B;IAC3B,4BAA4B;IAC5B,aAAa;IACb,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,uBAAuB;IACvB,kBAAkB;IAClB,aAAa;IACb,YAAY;AAChB;;AAEA;;IAEI,kBAAkB;AACtB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,4CAA4C;IAC5C,uBAAuB;IACvB,iBAAiB;IACjB,qBAAqB;IACrB,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,iCAAiC;IACjC,cAAc;IACd,sCAAsC;AAC1C;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,YAAY;AAChB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;;;IAGI,qDAAqD;AACzD;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,YAAY;AAChB;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,uBAAuB;IACvB,YAAY;IACZ,kBAAkB;IAClB,WAAW;IACX,SAAS;AACb;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI;QACI,aAAa;IACjB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,gBAAgB;IACpB;;IAEA;QACI,cAAc;IAClB;;IAEA;QACI,mBAAmB;IACvB;;IAEA;QACI,oBAAoB;IACxB;AACJ;;AAEA;IACI;QACI,gBAAgB;IACpB;;IAEA;QACI,eAAe;QACf,WAAW;IACf;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,cAAc;IAClB;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,kBAAkB;IACtB;AACJ","sourcesContent":["html {\n    box-sizing: border-box;\n}\n\n*, *:before, *:after {\n    box-sizing: inherit;\n}\n\nbody {\n    margin: 0;\n}\n\n#content {\n    min-height: 100vh;\n    position: relative;\n}\n\n.header-section {\n    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(../assets/yves-alarie-BXk-N2fPLYo-unsplash.jpg);\n    background-size: cover;\n    background-position: 0% 35%;\n    background-repeat: no-repeat;\n    height: 400px;\n    display: grid;\n    place-items: center;\n}\n\n.header-text-container {\n    border: 6px solid white;\n    width: fit-content;\n    padding: 20px;\n  