___CSS_LOADER_EXPORT___.push([module.id, `.menu-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-bottom: 70px;
}

.image-container {
    position: relative;
}

.left-image-square,
.right-image-square {
    position: absolute;
    background-size: cover;
    background-repeat: no-repeat;
    top: 30px;
}

.left-image-square:hover,
.right-image-square:hover {
    transform: scale(1.05);
}

.left-image-square {
    left: 30px;
    background-position: 80% 0%;
}

.right-image-square {
    right: 30px;
    background-position: 60% 0%;
}

.border-square,
.left-image-square,
.right-image-square {
    border: 5px solid rgb(67, 87, 61);
    height: 300px;
    width: 300px;
}

.meal-header {
    font-size: 30px;
    font-family: "Times New Roman", sans-serif, monospace;
    color: white;
    text-align: center;
    padding: 40px 0;
    background-color: black;
    position: sticky;
    top: 0;
    z-index: 1;
}

.meal-header:not(:first-child){
    margin: 40px 0;
}

.meal-header::before {
    content: "-";
}

.meal-header::after {
    content: "-";
}

.image-txt-container {
    display: flex;
    gap: 15%;
    background-color: white;
    height: 400px;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

.text-container.left-side{
    text-align: right;
}

.text-container {
    max-width: 500px;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.text-container,
.price {
    font-size: 1.3rem;
}

.subtext-header {
    font-size: 2rem;
    font-weight: bold;
    color: rgb(67, 87, 61);
}

.price {
    font-style: italic;
    font-weight: bold;
}

@media screen and (max-width: 800px) {
    .image-txt-container {
        flex-direction: column;
        height: fit-content;
        justify-content: flex-start;
    }

    .text-container {
        margin: 40px 0;
    }
}

@media screen and (max-width: 400px) {
    .border-square,
    .left-image-square,
    .right-image-square {
        height: 200px;
        width: 200px;
    }
}`, "",{"version":3,"sources":["webpack://./src/style-sheets/menu.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,oBAAoB;AACxB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;;IAEI,kBAAkB;IAClB,sBAAsB;IACtB,4BAA4B;IAC5B,SAAS;AACb;;AAEA;;IAEI,sBAAsB;AAC1B;;AAEA;IACI,UAAU;IACV,2BAA2B;AAC/B;;AAEA;IACI,WAAW;IACX,2BAA2B;AAC/B;;AAEA;;;IAGI,iCAAiC;IACjC,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,qDAAqD;IACrD,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,uBAAuB;IACvB,gBAAgB;IAChB,MAAM;IACN,UAAU;AACd;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,QAAQ;IACR,uBAAuB;IACvB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;;IAEI,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,sBAAsB;AAC1B;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI;QACI,sBAAsB;QACtB,mBAAmB;QACnB,2BAA2B;IAC/B;;IAEA;QACI,cAAc;IAClB;AACJ;;AAEA;IACI;;;QAGI,aAAa;QACb,YAAY;IAChB;AACJ","sourcesContent":[".menu-content {\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    padding-bottom: 70px;\n}\n\n.image-container {\n    position: relative;\n}\n\n.left-image-square,\n.right-image-square {\n    position: absolute;\n    background-size: cover;\n    background-repeat: no-repeat;\n    top: 30px;\n}\n\n.left-image-square:hover,\n.right-image-square:hover {\n    transform: scale(1.05);\n}\n\n.left-image-square {\n    left: 30px;\n    background-position: 80% 0%;\n}\n\n.right-image-square {\n    right: 30px;\n    background-position: 60% 0%;\n}\n\n.border-square,\n.left-image-square,\n.right-image-square {\n    border: 5px solid rgb(67, 87, 61);\n    height: 300px;\n    width: 300px;\n}\n\n.meal-header {\n    font-size: 30px;\n    font-family: \"Times New Roman\", sans-serif, monospace;\n    color: white;\n    text-align: center;\n    padding: 40px 0;\n    background-color: black;\n    position: sticky;\n    top: 0;\n    z-index: 1;\n}\n\n.meal-header:not(:first-child){\n    marg