    width: 100%;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 0 70px 0;
}

.contact-info-container {
    min-height: 250px;
    max-height: fit-content;
    max-width: 1000px;
    width: 100%;
    border: 5px solid rgb(67, 87, 61);
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    margin: 10px;
}

.contact-info-container p {
    margin: 0;
}

.contact-info-container > div {
    text-align: center;
    font-size: 1.3rem;
}

.contact-info-container > div > p:first-child {
    margin-bottom: 10px;
}

.contact-header {
    font-size: 2rem;
    font-weight: bold;
    color: rgb(67, 87, 61);
}`, "",{"version":3,"sources":["webpack://./src/style-sheets/contact.css"],"names":[],"mappings":"AAAA;IACI,WAAW;IACX,aAAa;IACb,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;AAC1B;;AAEA;IACI,iBAAiB;IACjB,uBAAuB;IACvB,iBAAiB;IACjB,WAAW;IACX,iCAAiC;IACjC,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,SAAS;IACT,YAAY;AAChB;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,sBAAsB;AAC1B","sourcesContent":[".contact-content {\n    width: 100%;\n    height: 500px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    padding: 30px 0 70px 0;\n}\n\n.contact-info-container {\n    min-height: 250px;\n    max-height: fit-content;\n    max-width: 1000px;\n    width: 100%;\n    border: 5px solid rgb(67, 87, 61);\n    padding: 0 20px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    gap: 20px;\n    margin: 10px;\n}\n\n.contact-info-container p {\n    margin: 0;\n}\n\n.contact-info-container > div {\n    text-align: center;\n    font-size: 1.3rem;\n}\n\n.contact-info-container > div > p:first-child {\n    margin-bottom: 10px;\n}\n\n.contact-header {\n    font-size: 2rem;\n    font-weight: bold;\n    color: rgb(67, 87, 61);\n}"],"sourceRoot":""}]);
